import React from "react";
import {connect} from "react-redux";
import Users from './Users';
import {follow, toggleIsFetching, toggleFollowingInProgress, getUsers} from "../../redux/users-reducer";
import {unfollow} from "../../redux/users-reducer";
import {setPages} from "../../redux/users-reducer";
import {setCurrentPage} from "../../redux/users-reducer";
import Preloader from "../common/preloader/preloader";
import {withAuthRedirect} from "../../hoc/withAuthRedirect";
import {compose} from "redux";
import {getUsersSuper} from "../../redux/users-selectors";

class UsersContainer extends React.Component {
    componentDidMount() {
        this.props.getUsers(this.props.currentPage, this.props.pageSize);
    }
    onPageChanged = (pageNumber) => {
        this.props.setCurrentPage(pageNumber);
        this.props.getUsers(pageNumber, this.props.pageSize);
    }
    render() {
        return <div>
            {
                this.props.isFetching ? <Preloader /> : undefined
            }
            <Users currentPage={this.props.currentPage}
                   users={this.props.users}
                   follow={this.props.follow}
                   unfollow={this.props.unfollow}
                   onPageChanged={this.onPageChanged}
                   followingInProgress={this.props.followingInProgress}
                   toggleFollowingInProgress={this.props.toggleFollowingInProgress}
            />
        </div>
    }
}
const mapStateToProps = (state) => {
    return {
        users: getUsersSuper(state),
        pageSize: state.usersPage.pageSize,
        totalUsersCount: state.usersPage.totalUsersCount,
        currentPage: state.usersPage.currentPage,
        isFetching: state.usersPage.isFetching,
        followingInProgress: state.usersPage.followingInProgress
    }
};

/*const mapDispatchToProps = (dispatch) => {
    return {
        follow: (userId) => {
            dispatch(followAC(userId));
        },
        unfollow: (userId) => {
            dispatch(unfollowAC(userId));
        },
        setUsers: (users) => {
            dispatch(setUsersAC(users));
        },
        setPages: (pagesCount) => {
            dispatch(setPagesCountAC(pagesCount));
        },
        setCurrentPage: (currentPage) => {
            dispatch(setCurrentPageAC(currentPage));
        },
        toggleIsFetching: (isFetching) => {
            dispatch(toggleIsFetchingAC(isFetching))
        }
    }
};*/

export default compose(withAuthRedirect, connect(mapStateToProps, {follow, unfollow,
    setPages, setCurrentPage,
    toggleFollowingInProgress, getUsers}))(UsersContainer);