import React from "react";
import {Field, reduxForm} from "redux-form";
import {Input} from "../common/FormControl/FormControls";
import {maxLengthCreator, required} from "../../utils/validator/validators";
import {connect} from "react-redux";
import {login} from "../../redux/auth-reducer";
import {Redirect} from "react-router-dom";
import style from '../common/FormControl/FormControls.module.css';

const Login = (props) => {
    const onSubmit = (formData) => {
        props.login(formData.email, formData.password, formData.rememberMe)
    }
    if (props.isAuth) {
        return <Redirect to={'/profile'} />
    }
    return <div>
        <LoginReduxForm onSubmit={onSubmit}/>
    </div>
}

const maxLength30 = maxLengthCreator(30);

const LoginForm = (props) => {
    return <div>
        <h1>Login</h1>
        <form onSubmit={props.handleSubmit}>
            <div>
                <Field placeholder={'Email'} name={'email'} component={Input} validate={[required, maxLength30]}/>
            </div>
            <div>
                <Field placeholder={'Password'} type={'password'} name={'password'} component={Input} validate={[required, maxLength30]}/>
            </div>
            <div>
                <Field name={'rememberMe'} component={'input'} type={'checkbox'}/> remember me
            </div>
            {props.error && <div className={style.summaryErrorForm}>
                {props.error}
            </div>}
            <div>
                <button>Login</button>
            </div>
        </form>
    </div>
}

const LoginReduxForm = reduxForm({form: 'login'})(LoginForm);

const mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth
})

export default connect(mapStateToProps, {login})(Login);