import s from './Dialogs.module.css';
import React from "react";
import DialogItem from "./DIalogItem/DialogItem";
import Messages from "./Messages/Messages";
import Login from "../Login/Login";
import {Field, reduxForm} from "redux-form";
import {Textarea} from "../common/FormControl/FormControls";
import {maxLengthCreator, required} from "../../utils/validator/validators";

const maxLength50 = maxLengthCreator(50);

const TextArea = (props) => {
    return <form onSubmit={props.handleSubmit}>
        <Field component={Textarea} name={'newMessageBody'} validate={[required, maxLength50]}
        placeholder={'WOW'}/>
        <button >Shmyak</button>
    </form>
}

const TextAreaRedux = reduxForm({form: 'adddMessageForm'})(TextArea);

const Dialogs = (props) => {
    let dialogs = props.messagesPage.dialog.map( d => <DialogItem name={d.name} key={d.id} id={d.id}/>);
    let messages = props.messagesPage.messages.map( message => <Messages message={message.message} key={message.id} id={message.id}/>);
    let newMessageBody = props.messagesPage.newMessageText;

    let addMessage = () => {
        props.addMessage();
    };
    let updateText = (event) => {
        let text = event.target.value;
        props.updateText(text);
    }
    let addNewMessage = (data) => {
        props.addMessage(data.newMessageBody);
    }
    if(!props.isAuth) {
        return <Login />
    }
    return (
        <div className={s.content}>
            <div className={s.dialogs}>
                <div className={s.dialogItems}>
                    { dialogs }
                </div>

                <div className={s.messages}>
                    { messages }
                </div>
                <div>
                    <TextAreaRedux onSubmit={addNewMessage}/>
                    {/*<textarea  onChange={updateText} value={newMessageBody}>
                    </textarea>
                    <button >Shmyak</button>*/}
                </div>
            </div>
        </div>

    );
}

export default Dialogs;