const ADD_MESSAGE = 'ADD-MESSAGE';
const UPDATE_MESSAGE = 'UPDATE-MESSAGE';

const initialState = {
    dialog: [
        {name: 'Hloe', id: 1},
        {name: 'Raw', id: 2},
        {name: 'Hloe', id: 3}
    ],
    newMessageText: '',
    messages:
        [
            {message: 'Hloe', id: 1},
            {message: 'Aloe', id: 2},
            {message: 'Hloe', id: 3}
        ]
};

const dialogsReducer = (state = initialState, action) => {
    switch (action.type) {
        case UPDATE_MESSAGE: {
            return  {
                ...state,
                newMessageText: action.message
            };
        }
        case ADD_MESSAGE: {
            return {
                    ...state,
                    messages: [...state.messages, { message: action.newMessageBody, id: 5}],
                    newMessageText: ''
                };
        }
        default:
            return state;

    }
}

export default dialogsReducer;

export const updateMessageCreator = (text) => {
    return {
        type: UPDATE_MESSAGE,
        message: text
    }
}
export const addMessageCreator = (newMessageBody) => ({type: ADD_MESSAGE, newMessageBody});